.App-logo {
  transform: scale(calc(40vh));
  z-index: 1000;
}
.btn {
  background: rgba(196, 196, 196, 0.5);
    box-shadow: 0px 2px 2px rgba(123, 50, 37, 0.33);
    border-radius: 13px;
    padding: 2% 8%;
    margin-top: 5%;
}
.main {
  width: min-content;
  padding: 3% 8% ;
  display: flex;
  background-color: white;
  flex-direction: column;
  align-items: center;
}
h1 {
    color: white;
    letter-spacing: 1.5px;
    font-size: calc(2px + 2vmin);
    margin-top: 1%;
}

.main2 {
  padding: 5%;
  height: 80vh;
  background-color: #c4c4c4;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 25px;
}

.contentmain1 {
  display: flex;
  flex-direction: column;
  background-color: red;
  min-width: 35vh;
  min-height: 28vh;
  justify-content: space-around;
  align-items: center;
  z-index: 0;
  border-radius:50px;
  padding: 9% 0px;
}
.contentmain2 {
  display: flex;
  flex-direction: column;
  background-color: #27838e;
  min-width: 35vh;
  min-height: 28vh;
  justify-content: space-around;
  align-items: center;
  margin-top: 40px; 
  border-radius: 50px;
  padding: 9% 0px;
}
