.troyaBack {
    background-color: #27838E;
    height: 80vh;
    width: 45vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    
}
.tagHead {
    font-size: calc(10px + 1vmin);
    margin-top: 10px;
    max-width: 35vh;
}
.image-container {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-around;
}
.tagContent {
    font-size: calc(6px + 1vmin);
    max-width: 35vh;
}
.portrait {
    margin-bottom: 3vh;
}
.troya-close {
    margin-top: -18vh;
    margin-bottom: 18vh;
    align-self: flex-end;
}
.click-pass {
    margin-bottom: 4vh;
}
.click-upper {
    min-width: 5vh;
    display: flex;
    justify-content: center;
}

.container-troya {
    display: flex;
    background-color: white;
    padding: 3%;
} 

.btn-tr { 
    background-color: transparent;
    margin-top: 5%;
 }