.polyska-main {
  background-color: #D44A32;
  height: 80vh;
  width: 45vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 15px;  
}
.main-photos {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}
.clicker {
  margin-bottom: 3vh;
}

.tagHead {
  font-size: calc(10px + 1vmin);
  margin-top: 10px;
  max-width: 35vh;
}
.tagContent {
  font-size: calc(4px + 1vmin);
  max-width: 35vh;
}
.portrait {
  margin-bottom: 3vh;
}
.middlePic {
  min-width: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
